.profile {
  &-dark {
    min-height: 100vh;
    color: $soft-white;
    background-color: $grey-36;
    position: relative;
    transition: all 0.3s ease;

    & a {
      color: $soft-white;
    }

    .certs-item {
      &-normal-text {
        border-right: 1px solid $soft-white;
      }
      &-reverse-text {
        border-left: 1px solid $soft-white;
      }
    }

    border-bottom: 1px solid grey;
  }

  &-light {
    color: $grey-36;
    background-color: $soft-white;
    min-height: 100vh;
    border-bottom: 1px solid grey;
    position: relative;
    transition: all 0.3s ease;

    & a {
      color: $grey-36;
    }

    .intro-img {
      border: 7px solid $grey-36;
    }
  }

  &-midnight {
    color: rgb(209, 208, 208);
    background-color: rgb(7, 7, 7);
    min-height: 100vh;
    border-bottom: 1px solid grey;
    position: relative;
    transition: all 0.3s ease;

    & a {
      color: $soft-white;
    }

    .certs-item {
      &-normal-text {
        border-right: 1px solid $soft-white;
      }
      &-reverse-text {
        border-left: 1px solid $soft-white;
      }
    }
  }
}

.content-scroll {
  color: white;
  text-decoration: none;

  &:hover {
    color: rgb(182, 182, 182);
    cursor: pointer;
  }
}

.helper-text {
  font-family: $font-rob;
  position: absolute;
  bottom: 10px;
  right: 10px;
  font-size: 17px;
}
